<template>
  <div class="current">
        <main>
            <p class="description">{{ $store.getters.currentWeather.condition.text }}</p>
            <div :class="mapIcon($store.getters.currentWeather.condition.icon)" class="icon"></div>
            <div class="temp">
                <p class="avg">{{ $store.getters.currentWeather.temp_c + "\xB0C" }}</p>
                <div class="minor">
                    <p>
                        <i class="fa fa-caret-up" aria-hidden="true"></i>
                        {{ $store.getters.forecast.forecastday[0].day.maxtemp_c + "\xB0C" }}
                    </p>
                    <p>
                        <i class="fa fa-caret-down" aria-hidden="true"></i>
                        {{ $store.getters.forecast.forecastday[0].day.mintemp_c + "\xB0C" }}
                    </p>
                </div>
            </div>
        </main>
    </div>
</template>

<script>
import {iconMap as apixuMappings} from './iconMap';
export default {
  name: "current",
  methods:{
        mapIcon(url){
            const re = /(?!\/)(\d{3})(?!\w{3})/;
            return apixuMappings[re.exec(url)[0]];
        },
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.current{
    margin: calc(50px + 4em) 2em 2em 2em;
    text-align: center;
    main{
        height: calc(100vh - (50px + 6em));
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        .description{
            text-transform: uppercase;
            letter-spacing: 0.5em;
            font-size: 1.5em;
            font-weight: 300;
            text-align: center;
        }
        .icon{
            font-size: 8em;
            margin: 0.2em 0 0.3em 0;
        }
        .temp{
            display: flex;
            flex-direction: row;
            justify-content: center;
            letter-spacing: 0.1em;
            .avg{
                font-size: 4em;
                margin: 0;
                font-weight: 200;
            }
            .minor{
                display: flex;
                flex-direction: column;
                font-weight: 500;
                margin: 0 0 0 0.5em;
                p{
                    margin: 0.5em 0 0.5em 0;
                    .fa-caret-up{
                        color: #FF5722
                    }
                    .fa-caret-down{
                        color: #3F51B5
                    }
                }
            }
        }
    }
}
</style>



