<template>
    <div class="details">
        <main>
            <div class="row">
                <temperature></temperature>
                <humidity></humidity>
                <uvindex></uvindex>
            </div>
            <div class="row">
                <wind></wind>
                <precip></precip>
                <pressure></pressure>
            </div>
        </main>
    </div>
</template>

<script>

    import temperature from './details/temperature';
    import humidity from './details/humidity';
    import uvindex from './details/uvindex';
    import wind from './details/wind';
    import precip from './details/precip';
    import pressure from './details/pressure';

    export default {
        name: 'details',
        components: {
            temperature,
            humidity,
            uvindex,
            wind,
            precip,
            pressure
        },
        data() {
            return {
                index: this.$store.getters.activeDay,
            };
        },
        methods:{
            calc(data, type){
                return parseInt(
                    data.map(data => data[type])
                        .reduce((prev, next) => prev + next )/data.length
                );
            }
        },
    };
</script>

<style lang="scss">
    .details {
        width: 100%;
        height: auto;
        background: #F5F5F5; 
        main {
            margin: 1em 2em 1em 2em;
            display: grid;
            .row {
                display: flex;
                flex-direction: row;
                justify-content: space-between;
                /*margin: 5px 0 5px 0;*/
            }
        }
    }
</style>



