<template>
    <div class="wind detail">
        <i class="wi wi-strong-wind icon"></i>
        <p class="value">{{ calc(this.$store.getters.forecast.forecastday[$store.getters.activeDay].hour, "wind_kph") }}</p>
        <p class="text">Wind</p>
    </div>
</template>

<script>
    export default {
        name: "wind",
        methods: {
            calc(data, type) {
                return parseInt(
                    data.map(data => data[type])
                        .reduce((prev, next) => prev + next) / data.length
                );
            }
        },
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
     @import "details-style.scss"
</style>



