<template>
    <div class="pressure detail">
        <i class="wi wi-barometer icon"></i>
        <p class="value">{{ calc(this.$store.getters.forecast.forecastday[$store.getters.activeDay].hour, "pressure_mb") }}</p>
        <p class="text">Pressure</p>
    </div>
</template>

<script>
    export default {
        name: 'pressure',
        methods: {
            calc(data, type) {
                return parseInt(
                    data.map(data => data[type])
                    .reduce((prev, next) => prev + next) / data.length
                );
            }
        },
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
     @import "details-style.scss"
</style>



