<template>
  <div class="units">
        <main>
            <button v-on:click="toggleSetting()" class="setting">
                <p>Units</p>
                <div class="values">
                    <div class="arrow-down"></div>
                </div>
            </button>
            <div v-if="this.toggle === true" class="content">
                <form>
                    <div>
                        <input type="radio" value="metric" v-model="$store.getters.units" v-on:change="changeUnits('metric')">
                        <label>Metric</label>
                    </div>
                    <div>
                        <input type="radio" value="imperial" v-model="$store.getters.units" v-on:change="changeUnits('imperial')">
                        <label>Imperial</label>
                    </div>
                </form>
            </div>
        </main>
    </div>
</template>

<script>

export default {
  name: "units",
  data(){
      return{
          toggle: false,
      }
  },
  methods:{
      toggleSetting(){
          return this.toggle = !this.toggle;
      },
      changeUnits(units){
          this.$store.dispatch('SET_UNITS', units);
      }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.units{
    button:focus {outline:0;}
    border-bottom: solid 1px #E0E0E0;
    .content{
        background: #FFF;
        form{
            display: flex;
            flex-direction: column;
        }
        form > div {
                margin: 1em auto 1em 2em;
        }
    }
    .setting{
        text-align: left;
        width: 100%;
        padding: 1em 1em 1em 1em;
        font-size: 1em;
        border: none;
        background: none;
        margin: 0;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        .arrow-down{
            background: url("../../../assets/arrow-down.svg");
            background-size: contain;
            background-repeat: no-repeat;
            width: 1em;
            height: 1em;
            }
            .values{
                display: flex;
                .value{
                    margin: 0 1em 0 0;
                }
            }
            p{
                margin: 0;
                padding: 0;
            }
        }
}
</style>



