<template>
    <div class="precip detail">
        <i class="wi wi-umbrella icon"></i>
        <p class="value">{{ calc(this.$store.getters.forecast.forecastday[$store.getters.activeDay].hour, "precip_mm") }}</p>
        <p class="text">Precip</p>
    </div>
</template>

<script>
      export default {
          name: "precip",
          methods: {
              calc(data, type) {
                  return parseInt(
                      data.map(data => data[type])
                      .reduce((prev, next) => prev + next) / data.length
                  );
              }
          },
      }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
    @import "details-style.scss"
</style>



