<template>
    <div class="calendar">
        <main>
            <div class="wrapper">
                <div class="day" v-on:click="changeDay(index)" v-for="(item, index) in this.$store.getters.forecast.forecastday">
                    <div v-if="$store.getters.activeDay !== index" class="not-active">
                        <offset></offset>
                        <div :class="mapIcon(item.day.condition.icon)" class="icon"></div>
                        <p> {{ mapDate(item.date, index) }} </p>
                        <offset></offset>
                    </div>
                    <div v-else class="active">
                        <offset></offset>
                        <div :class="mapIcon(item.day.condition.icon)" class="icon"></div>
                        <p> {{ mapDate(item.date, index) }} </p>
                        <offset></offset>
                    </div>
                </div>
            </div>
        </main>
    </div>
</template>

<script>
    import {iconMap as apixuMappings} from './iconMap';

    export default {
        name: 'calendar',
        components: {
    
        },
        data() {
            return {
    
            };
        },
        methods:{
            mapIcon(url){
                const re = /(?!\/)(\d{3})(?!\w{3})/;
                return apixuMappings[re.exec(url)[0]];
            },
            mapDate(date, index){
                if(index === 0) 
                    return "Today"
                else 
                    return new Date(date).toDateString().slice(0, 3);
            },
            changeDay(index){
                this.$store.dispatch('SET_ACTIVE_DAY', index)
            }
        }
    };
</script>

<style lang="scss">
    .calendar {
        background: #F5F5F5;
        main {
            .wrapper {
                display: flex;
                flex-direction: row;
                justify-content: flex-start;
                overflow: auto;
                white-space: nowrap;
                .day {
                    margin: 0;
                    flex-shrink: 0;
                    display: flex;
                    flex-direction: column;
                    justify-content: space-around;
                    /*text-align: center;*/
                    cursor: pointer;
                    .icon {
                        font-size: 2em;
                        /*margin: 0.5em 0 0 0;*/
                        padding: 0 0 0.25em 0;
                        margin: auto auto;
                    }
                    p {
                        font-size: 1.2em;
                        padding: 0.25em 0 0 0;
                        margin: auto auto;
                    }
                    .active {
                        display: grid;
                        grid-template-rows: 1fr 1fr 1fr 1fr;
                        background: #3F51B5;
                        color: #F5F5F5;
                        width: 30vw;
                        height: 30vw;
                        /*padding: 0.5em;*/
                    }
                    .not-active{
                        /*display: flex;
                        flex-direction: column;
                        justify-content: space-around;*/
                        display: grid;
                        grid-template-rows: 1fr 1fr 1fr 1fr;
                        background: none;
                        width: 30vw;
                        height: 30vw;
                        /*padding: 0.5em;*/
                    }
                } 
            }
        }
    }
</style>



